.nav {
    position: fixed;
    top: 0;
    padding: 15px;
    width: 100%;
    height: 30px;
    z-index: 1; 
}


.nav__contents{
    display: flex;
    padding-left: 630px;
    padding-right: 30px;
    width: 300px;
    background-color: rgba(0, 0, 0, 0.6);
    background: url("https://thumbs.gfycat.com/ConsciousSpotlessKarakul-size_restricted.gif") center;
    border-top-left-radius: 50px;
    border-bottom-right-radius: 50px;
}
.nav__contents>a{
    color: white;
    margin: 10px;
}
