.netflixheader__bar{
    display: flex;
    justify-content: space-between;
    padding-top: 30px;
    padding-left: 50px;
    padding-right: 50px;
}

.netflixheader__bar>h1{
    color: rgb(223, 13, 13);
    border-bottom: 3px solid rgb(196, 17, 17);
    text-align: center;
    
}

.netflixheader__bar>button{
   height: 50px;
   width: 100px; 
   border-radius: 7px;
    border: none;
    background-color: rgb(23, 79, 231);
    color: white;
    cursor: pointer;
    font-weight: 600;
    font-size: 20px;
}
.netflixheader__bar>button:hover{
    background-color: rgb(12, 34, 92);
}
.bar__link>form>input{
    height:50px;
    width: 200px;
    border-radius: 7px;
    border: none;
    background-color: rgb(209, 12, 12);
    color: white;
    cursor: pointer;
    font-weight: 600;
    font-size: 20px;
}
.bar__link>form>input:hover{
    background-color: maroon;
}
.snaps>h2{
    font-weight: 500;
}
.netflix__row1{
    display: flex;
    padding-bottom: 40px;
}
.netflix__row1{
    margin-left: 95px;
    margin-right: 95px;
}
.netflix__row1>img{
    height: 300px;
    width: 500px;
    margin: auto;
    cursor: pointer;
    transition: transform 450ms;
    border-radius: 7px;   
}
.netflix__row1>img:hover{
    transform: scale(2.00);
    opacity: 1;
}
.netflix__row2{
    display: flex;
    padding-bottom: 60px;
    padding-left: 97px;
    padding-right: 97px;
}
.netflix__row2>img{
