.linkedinheader__bar>h1{
    color: rgb(23, 112, 228);
    border-bottom: 3px solid rgb(27, 87, 216);
    text-align: center;
    
}
.linkedinheader__bar{
    display: flex;
    justify-content: space-between;
    padding-top: 30px;
    padding-left: 50px;
    padding-right: 50px;
}

.linkedinheader__bar>button{
   height: 50px;
   width: 100px; 
   border-radius: 7px;
    border: none;
    background-color: rgb(23, 79, 231);
    color: white;
    cursor: pointer;
    font-weight: 600;
    font-size: 20px;
}
.linkedinheader__bar>button:hover{
    background-color: rgb(12, 34, 92);
}
.linkedinbar__link>form>input{
    height:50px;
    width: 200px;
    border-radius: 7px;
    border: none;
    background-color: rgb(45, 133, 235);
    color: white;
    cursor: pointer;
    font-weight: 600;
    font-size: 20px;
}
.linkedinbar__link>form>input:hover{
    background-color: rgb(22, 66, 211);
}
.snaps>h2{
    font-weight: 500;
}
.row1{
    display: flex;
    padding-bottom: 40px;
}
.row1>img{
    height: 300px;
    width: 500px;
    margin: auto;
    cursor: pointer;
    transition: transform 450ms;
    border-radius: 7px;   
}
.row1>img:hover{
    transform: scale(2.00);
    opacity: 1;
}
